<template>
  <el-main>
    <el-row>
      <el-col :span="12" class="toolbar" style="padding-left: 10px;font-size: 30px">
        <el-form :model="selectForm" ref="selectForm" id="select">
          <!--   选择学期  -->
          <el-select v-model="selectForm.term"  placeholder="请选择学期">
            <el-option v-for="item in selectForm.terms"
              :label="item.termText"
              :key="item.value"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <!--   选择课程  -->
          <el-select v-model="selectForm.course" placeholder="请选择课程" style="padding-left: 60px">
            <el-option v-for="item in courseInfo"
              :label="item.courseName"
              :key="item.courseId"
              :value="item.courseId"
            >
            </el-option>
          </el-select>
        </el-form>
      </el-col>
      <el-col :span="3" class="toolbar" style="padding-left: 20px;font-size: 30px">
        <el-button type="primary" size="big" class="del-com" @click="getCheck">确认</el-button>
      </el-col>
    </el-row>

    <el-table
      :data="scores"
      ref="multipleTable"
      tooltip-effect="dark"
      style="width: 100%; margin-top: 30px"
    >
      <el-table-column label="序号" type="index" align="center"></el-table-column>
      <el-table-column prop="courseId" label="课程号" align="center"></el-table-column>
      <el-table-column prop="courseName" label="课程名" align="center"></el-table-column>
      <el-table-column prop="major" label="专业" align="center"></el-table-column>
      <el-table-column prop="id" label="学号" align="center"></el-table-column>
      <el-table-column prop="name" label="姓名" align="center"></el-table-column>
      <el-table-column prop="score" label="成绩" align="center">
        <template slot-scope="scope" >
          <el-input v-model="scope.row.score"></el-input>
        </template>
      </el-table-column>
    </el-table>
<!--    <el-table-column label="操作" width="200" style="margin-top: 40px" align="center">-->
<!--      <el-button type="primary" size="big" class="del-com" @click="updateStuGrade" >提交</el-button>-->
<!--    </el-table-column>-->
  </el-main>
</template>

<script>
import apiUrl from "../constant/apiUrl";
import {request} from '../network/axios'
import { globalRespCode } from '../constant/respCode'
import { mapGetters } from 'vuex'

export default {
  name: "UpdateGrade",
  data () {
    return {
      selectForm: {
        terms: [{
          value: '2016-2017第一学期',
          label: '2016-2017第一学期'
        }, {
          value: '2016-2017第二学期',
          label: '2016-2017第二学期'
        }, {
          value: '2017-2018第一学期',
          label: '2017-2018第一学期'
        }, {
          value: '2017-2018第二学期',
          label: '2017-2018第二学期'
        }, {
          value: '2018-2019第一学期',
          label: '2018-2019第一学期'
        }, {
          value: '2018-2019第二学期',
          label: '2018-2019第二学期'
        }, {
          value: '2019-2020第一学期',
          label: '2019-2020第一学期'
        }, {
          value: '2019-2020第二学期',
          label: '2019-2020第二学期'
        }],
        term: '',
        course: ''
      },
      scores: [
        {
          courseId: '01',
          courseName:'UML',
          major:'software',
          id:'2017214865',
          name:'purple',
          score:'85'
        }, {
          courseId: '03',
          courseName: 'UML',
          major: 'software',
          id: '2017214853',
          name: 'Jenny',
          score: '94'
        }
      ],
    }
  },
  methods: {
    getCheck() {
      //发送学期、课程名
      //得到该课程的所有学生成绩
      let requestBody = {
        courseId : this.selectForm.course,
        term: this.selectForm.term,
        page: this.page,
        pageSize: this.pageSize
      }
      request({
        url: apiUrl.teacherGetGrades,
        method: 'get',
        requestBody
      }).then(res => {
        const result = res.data
        if (result.code === globalRespCode.SUCCESS) {
          this.scores = result.data.scores
          this.total = result.data.total
        } else {
          this.$message({type: 'error', message: '获取该课程成绩失败！'})
        }
      }).catch(e => {
        this.$message({type: 'error', message: '网路错误！'})
      })
    },
    //双向绑定问题
    updateStuGrade() {
      request({
        url: apiUrl.teacherGetCourses,
        method: 'post',
        courseInfo : this.scores,
      }).then(res => {
        const result = res.data
        if (result.code === globalRespCode.SUCCESS) {
          this.$message({
            type: 'success',
            message: '提交成功！'
          })
        } else {
          this.$message({type: 'error', message: '提交失败！'})
        }
      }).catch(e => {
        this.$message({type: 'error', message: '网路错误！'})
      })
    }
  },
  computed: {
    ...mapGetters(['courseInfo'])
  }

}
</script>

<style scoped>

</style>
