<template>
  <el-main>
    <el-table
      :data="courseInfo.slice((page-1)*pageSize, page*pageSize)"
      ref="multipleTable"
      tooltip-effect="dark"
      style="width: 100%"
    >
      <el-table-column label="序号" width="100px" type="index"></el-table-column>
      <el-table-column prop="courseId" label="课程号"></el-table-column>
      <el-table-column prop="courseName" label="课程名"></el-table-column>
      <el-table-column prop="major" label="专业" sortable></el-table-column>
      <el-table-column prop="term" label="学期" sortable></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="seeStuGrade(scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>

    <div class="block">
      <el-row type="flex" justify="center">
        <el-pagination
            @current-change="currentPageChange"
            :hide-on-single-page="true"
            :current-page="this.page"
            :page-size="pageSize"
            :total="total">
        </el-pagination>
      </el-row>
    </div>

    <el-dialog title="成绩单" :visible.sync="getDetailsVisible" :close-on-click-modal="false">
      <el-table
        :data="scores"
        ref="multipleTable"
        tooltip-effect="dark"
        style="width: 100%"
      >
        <el-table-column label="排名" width="100px" type="index"></el-table-column>
        <el-table-column prop="id" label="学号"></el-table-column>
        <el-table-column prop="name" label="姓名"></el-table-column>
        <el-table-column prop="major" label="专业"></el-table-column>
        <el-table-column prop="score" label="成绩"></el-table-column>
        <el-table-column prop="term" label="排名"></el-table-column>
      </el-table>
    </el-dialog>
  </el-main>
</template>

<script>
import apiUrl from "../constant/apiUrl";
import {request} from '../network/axios'
import { globalRespCode } from '../constant/respCode'
import { mapGetters } from 'vuex'

export default {
  name: "SeeGrade",
  data() {
    return {
      getDetailsVisible: false,
      total: 5,
      page: 1,
      pageSize: 2,
      courses: [
        {
          courseId: '01',
          courseName:'UML',
          major:'software',
          term:'2017-2018 第二学期'
        },
        {
          courseId: '02',
          courseName:'需求工程',
          major:'software',
          term:'2019-2020 第一学期'
        }
      ],
      scores: [{
        id:'2017214865',
        name:'purple',
        score:'85',
        major:'software',
        rank:'23'
      }]
    }
  },
  methods:{
    seeStuGrade (row) {
      this.getDetailsVisible = true
      let requestBody = {
        courseId : row.courseId,
        page: this.page,
        pageSize: this.pageSize
      }
      request({
        url: apiUrl.teacherGetGrades,
        method: 'get',
        requestBody
      }).then(res => {
        const result = res.data
        if (result.code === globalRespCode.SUCCESS) {
          this.scores = result.data.scores
          this.total = result.data.total
        } else {
          this.$message({type: 'error', message: '获取成绩列表失败！'})
        }
      }).catch(e => {
        this.$message({type: 'error', message: '网路错误！'})
      })
        //发送课程号
        //得到所有选这门课学生的成绩单，（id,name,major,grade,rank），平均分
    },

    currentPageChange (val) {
      this.page = val
    }
  },
  computed: {
    ...mapGetters(['courseInfo'])
  }
}
</script>

<style scoped>

</style>
