import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login'
import Admin from "../components/Admin";
import Teacher from "../components/Teacher";
import Student from "../components/Student";
import store from '../store'
import SeeGrade from "../components/SeeGrade";
import UpdateGrade from "../components/UpdateGrade";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    redirect: '/login'
  }, {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      requireAuth: false
    }
  }, {
    path: '/admin',
    name: 'Admin',
    component: Admin,
    meta: {
      requireAuth: false
    }
  }, {
    path: '/teacher',
    name: 'Teacher',
    component: Teacher,
    meta: {
      requireAuth: false
    },
    redirect: '/teacher/SeeGrade',
    children: [
      {
        path: '/teacher/SeeGrade',
        name: 'SeeGrade',
        component: SeeGrade,
      },
      {
        path: '/teacher/UpdateGrade',
        name: 'UpdateGrade',
        component: UpdateGrade,
      }
    ]

}, {
    path: '/Student',
    name: 'Student',
    component: Student,
    meta: {
      requireAuth: false
    }
  }
]

const router = new VueRouter({
  // mode: 'history',
  //base: process.env.BASE_URL,
  routes
})

