import {HAS_LOGIN_MUTATION, MAJOR_INFO_MUTATION, COURSE_INFO_MUTATION} from "./mutation-types";
export default {
  [HAS_LOGIN_MUTATION] (state, user) {
    state.user = user
  },
  [MAJOR_INFO_MUTATION] (state, majors) {
    state.majors = majors
  },
  [COURSE_INFO_MUTATION] (state, courses) {
    state.courses = courses
  }
}
