import {HAS_LOGIN_MUTATION, MAJOR_INFO_MUTATION, COURSE_INFO_MUTATION} from "./mutation-types";

export default {
  async loginAction ({ state, commit }, {user, majors, courses}) {
    commit([HAS_LOGIN_MUTATION], user)
    commit([MAJOR_INFO_MUTATION], majors)
    commit([COURSE_INFO_MUTATION], courses)
    sessionStorage.setItem('Authorization', JSON.stringify(user.Authorization))
    sessionStorage.setItem('role', JSON.stringify(user.role))
  },

  async loginOutAction ({ state, commit }) {
    sessionStorage.removeItem('Authorization');
    sessionStorage.removeItem('role');
  }
}
