<template>
  <el-main>
    <el-row>
      <el-col :span="3" class="toolbar" style="padding-left: 10px;font-size: 40px">
        <el-menu default-active="1" class="el-menu-vertical-demo" style="min-height:900px" >
          <el-menu-item index="1"><i class="el-icon-message"></i>查看成绩</el-menu-item>
        </el-menu>
      </el-col>
      <el-col :span="18" class="toolbar" style="padding-left: 10px;font-size: 30px">
        <el-row>
          <el-col :span="12" class="toolbar" style="padding-left: 10px;font-size: 30px">
            <el-form id="select">
              <!--   选择学期  -->
              <el-select v-model="term"  placeholder="请选择学期">
                <el-option v-for="item in terms"
                  :label="item.termText"
                  :key="item.value"
                  :value="item.value"
                >
                </el-option>
              </el-select>
              <!--   选择类型  -->
              <el-select v-model="courseType" placeholder="请选择类型"  style="padding-left: 60px">
                <el-option v-for="item in types"
                  :label="item.typeText"
                  :key="item.value"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form>
          </el-col>
          <el-col :span="4" class="toolbar" style="padding-left: 20px;font-size: 30px">
            <el-button size="big" type="primary" class="del-com" @click="getCheck" >查询</el-button>
          </el-col>

          <el-col :span="3" class="toolbar" style="padding-right: 20px;font-size: 18px;margin-top: 20px">学期排名：{{termRank}}
          </el-col>
          <el-col :span="3" class="toolbar" style="padding-right: 20px;font-size: 18px;margin-top: 20px">学期绩点：{{termGPA}}
          </el-col>
        </el-row>

        <el-row style="margin-top: 50px">
          <el-col :span="5" class="toolbar" style="padding-left: 20px;font-size: 20px;">课程号</el-col>
          <el-col :span="5" class="toolbar" style="padding-left: 20px;font-size: 20px;">课程名</el-col>
          <el-col :span="5" class="toolbar" style="padding-left: 20px;font-size: 20px;">成绩</el-col>
          <el-col :span="5" class="toolbar" style="padding-left: 20px;font-size: 20px;">绩点</el-col>
        </el-row>
        <el-collapse v-model="flag" accordion>
          <el-collapse-item v-for="score in scores" :key="score.value">
            <template slot="title">
              <template>
                <el-col :span="5" class="toolbar" style="margin-left: 20px;font-size: 18px;">{{score.courseID}}</el-col>
                <el-col :span="5" class="toolbar" style="font-size: 18px;">{{score.courseName}}</el-col>
                <el-col :span="5" class="toolbar" style="font-size: 18px;">{{score.grade}}</el-col>
                <el-col :span="5" class="toolbar" style="font-size: 18px;">{{score.GPA}}</el-col>
              </template>
            </template>
            <el-row>
              <el-col :span="5" class="toolbar" style="padding-left: 18px;">平时成绩：{{score.grade1}}</el-col>
              <el-col :span="5" class="toolbar" style="padding-left: 18px;">期末考试成绩：{{score.grade2}}</el-col>
              <el-col :span="5" class="toolbar" style="padding-left: 18px;">实验成绩：{{score.grade3}}</el-col>
              <el-col :span="5" class="toolbar" style="padding-left: 18px;">排名：{{score.rank}}</el-col>
            </el-row>
          </el-collapse-item>
        </el-collapse>
      </el-col>
    </el-row>
  </el-main>
</template>

<script>
import apiUrl from '../constant/apiUrl'
import {request} from '../network/axios'
import { globalRespCode } from '../constant/respCode'

export default {
  name: 'Student',
  data () {
    return{
      term:'',
      courseType:'',
      flag:'0',
      termGPA: '3.7',
      termRank:'14',
      terms:[
        {value:1,termText:"2019-2020第一学期"},
        {value:2,termText:"2018-2019第二学期"},
        {value:3,termText:"2018-2019第一学期"}
      ],
      types:[
        {value:1,typeText:"b"},
        {value:2,typeText:"x"}
      ],
      scores: [{
        value:1,
        id:'2017214865',
        name:'purple',
        courseID:'01',
        courseName:'math',
        grade:'90',
        grade1:'93',
        grade2:'85',
        grade3:'90',
        GPA:'3.7',
        rank:'15'
      }, {
        value:2,
        id:'2017214851',
        name:'bob',
        courseID:'01',
        courseName:'math',
        grade:'96',
        grade1:'95',
        grade2:'98',
        grade3:'93',
        GPA:'4.0',
        rank:'5'
      }]
    }
  },
  methods: {
    //根据学期、课程类型获取学生个人成绩表单
    getCheck(){
      let requestBody = {
        term: this.term,
        courseType: this.courseType,
      }
      request({
        url: apiUrl.stuGetCourseGrades,
        method: 'get',
        requestBody
      }).then(res => {
        const result = res.data
        if (result.code === globalRespCode.SUCCESS) {
          this.scores = result.data.scores
        } else {
          this.$message({type: 'error', message: '获取成绩列表失败！'})
        }
      }).catch(e => {
        this.$message({type: 'error', message: '网路错误！'})
      })
    }
  }
}
</script>

<style scoped>
</style>
