<template>
  <el-main>
    <el-row>
      <el-col :span="3" class="toolbar" style="padding-left: 20px;font-size: 30px">
        <el-menu default-active="1" class="el-menu-vertical-demo" style="min-height:900px" @select="handleSelect">
          <el-menu-item index="1"><i class="el-icon-message"></i>查看成绩</el-menu-item>
          <el-menu-item index="2"><i class="el-icon-setting"></i>修改成绩</el-menu-item>
          <el-menu-item index="3"><i class="el-icon-menu"></i>个人信息</el-menu-item>
        </el-menu>
      </el-col>
      <el-col :span="18" class="toolbar" style="padding-left: 20px;font-size: 30px">
        <router-view></router-view>
      </el-col>
    </el-row>

  </el-main>
</template>

<script>
export default {
  name: 'Teacher',
  data() {
    return {
    }
  },
  methods: {
    //根据左侧菜单栏显示右侧信息
    handleSelect (key) {
      switch(key){
        case '1':
          this.$router.push('/teacher/SeeGrade');
          break;
        case '2':
          this.$router.push('/teacher/UpdateGrade');
          break;
        case '3':
          this.$router.push('/teacher/TeaPersonInfo');
          break;
      }
    }
  }
}
</script>

