<template>
  <el-main>
    <!-- 专业、年级、学期 -->
    <el-form :model="searchForm" ref="searchForm">
      <el-row>
        <el-col :span="6" class="toolbar" style="padding-left: 50px;">
          <el-form-item>
            <el-select v-model="searchForm.major" placeholder="请选择专业">
              <el-option
                  v-for="major in this.$store.getters.majors"
                  :key="major.majorName"
                  :label=major.majorName
                  :value="major.majorName">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6" class="toolbar" style="padding-left: 30px;">
          <el-form-item>
            <el-select v-model="searchForm.grade" placeholder="请选择年级">
              <el-option
                  v-for="grade in searchForm.grades"
                  :key="grade.value"
                  :label=grade.label
                  :value="grade.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6" class="toolbar" style="padding-left: 0px;">
          <el-form-item>
            <el-select v-model="searchForm.term" placeholder="请选择学期">
              <el-option
                  v-for="term in searchForm.terms"
                  :key="term.value"
                  :label="term.label"
                  :value="term.value"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6" class="toolbar" style="padding-left: 0px;">
          <el-form-item>
            <el-button type="primary" @click="queryScore">查询</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!-- 成绩列表 -->
    <el-table
      ref="multipleTable"
      :data="scores.slice((page-1)*pageSize, page*pageSize)"
      stripe
      style="width: 100%"
      height="300"
      @selection-change="handleSelectionChange"
    >
      <el-table-column prop="id" label="学号"></el-table-column>
      <el-table-column prop="name" label="姓名"></el-table-column>
      <el-table-column prop="avgScore" label="加权平均分" sortable></el-table-column>
      <el-table-column prop="gpa" label="绩点" sortable></el-table-column>
      <el-table-column prop="rank" label="专业排名"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="getDetails(scope.row)">详细</el-button>
        </template>
      </el-table-column>

    </el-table>
    <!-- 分页 -->
    <div class="block">
      <el-row type="flex" justify="center">
        <el-pagination
            @current-change="currentPageChange"
            :hide-on-single-page="true"
            :current-page="this.page"
            :page-size="pageSize"
            :total="total">
        </el-pagination>
      </el-row>
    </div>
    <!-- 学生成绩单 -->
    <el-dialog title="成绩单" :visible.sync="getDetailsVisible" :close-on-click-modal="false">
      <el-row>
        <el-col :span="4" class="toolbar" style="padding-left: 0px;">课程名称 </el-col>
        <el-col :span="4" class="toolbar" style="padding-left: 0px;">学分</el-col>
        <el-col :span="4" class="toolbar" style="padding-left: 0px;">成绩</el-col>
        <el-col :span="4" class="toolbar" style="padding-left: 0px;">绩点</el-col>
        <el-col :span="4" class="toolbar" style="padding-left: 0px;">课程排名</el-col>
      </el-row>
      <el-collapse v-model="flag" accordion>
        <el-collapse-item v-for="scoreDetail in scoreDetails">
          <template slot="title">
            <span style="padding-left: 33px">{{scoreDetail.course}}</span>
            <span style="padding-left: 90px">{{scoreDetail.credit}}</span>
            <span style="padding-left: 110px">{{scoreDetail.score}}</span>
            <span style="padding-left: 105px">{{scoreDetail.gpa}}</span>
            <span style="padding-left: 100px">{{scoreDetail.rank}}</span>
          </template>
          <el-row>
            <el-col :span="4" class="toolbar" style="padding-left: 20px;">平时分：{{scoreDetail.routine}}</el-col>
            <el-col :span="4" class="toolbar" style="padding-left: 20px;">作业分：{{scoreDetail.assignment}}</el-col>
            <el-col :span="4" class="toolbar" style="padding-left: 20px;">期末考试：{{scoreDetail.exam}}</el-col>
            <el-col :span="4" class="toolbar" style="padding-left: 20px;">实验分：{{scoreDetail.experiment}}</el-col>
          </el-row>
        </el-collapse-item>
      </el-collapse>
    </el-dialog>
  </el-main>
</template>

<script>
import apiUrl from "../constant/apiUrl";
import {request} from '../network/axios'
import { globalRespCode } from '../constant/respCode'
import { mapGetters } from 'vuex'

export default {
  name: "Admin",
  data () {
    return {
      searchForm: {
        /*majors: [{
          majorId: '软件工程',
          majorName: '软件工程'
        }, {
          value: '计算机科学与技术',
          label: '计算机科学与技术'
        }, {
          value: '通信工程',
          label: '通信工程'
        }, {
          value: '车辆工程',
          label: '车辆工程'
        }, {
          value: '土木工程',
          label: '土木工程'
        }, {
          value: '微电子',
          label: '微电子',
        }],
        grades: [{
          value: 2016,
          label: '2016级'
        }, {
          value: 2017,
          label: '2017级'
        },{
          value: 2018,
          label: '2018级'
        },{
          value: 2019,
          label: '2019级'
        },],*/
        terms: [{
          value: '2016-2017第一学期',
          label: '2016-2017第一学期'
        }, {
          value: '2016-2017第二学期',
          label: '2016-2017第二学期'
        }, {
          value: '2017-2018第一学期',
          label: '2017-2018第一学期'
        }, {
          value: '2017-2018第二学期',
          label: '2017-2018第二学期'
        }, {
          value: '2018-2019第一学期',
          label: '2018-2019第一学期'
        }, {
          value: '2018-2019第二学期',
          label: '2018-2019第二学期'
        }, {
          value: '2019-2020第一学期',
          label: '2019-2020第一学期'
        }, {
          value: '2019-2020第二学期',
          label: '2019-2020第二学期'
        }],
        major: '',
        grade: '',
        term: '',
      },
      scores: [{
        id: '2017214853',
        name: 'lyx',
        avgScore: '85',
        gpa: '3.7',
        rank: '20'
      },{
        id: '2017214853',
        name: 'lyx',
        avgScore: '85',
        gpa: '3.7',
        rank: '20'
      },{
        id: '2017214853',
        name: 'lyx',
        avgScore: '85',
        gpa: '3.7',
        rank: '20'
      },{
        id: '2017214853',
        name: 'lyx',
        avgScore: '85',
        gpa: '3.7',
        rank: '20'
      },{
        id: '2017214853',
        name: 'lyx',
        avgScore: '85',
        gpa: '3.7',
        rank: '20'
      }],
      scoreDetails:[{
        course: '数据结构',
        score: 87,
        credit: 5,
        gpa: '3.7',
        rank: '20/140',
        routine: 85,
        assignment: 85,
        exam: 80,
        experiment: 90
      },{
        course: '数据结构',
        score: 87,
        credit: 5,
        gpa: '3.7',
        rank: '20/140',
        routine: 85,
        assignment: 85,
        exam: 80,
        experiment: 90
      }],
      flag: '1',
      total: 5,
      page: 1,
      pageSize: 2,
      getDetailsVisible: false
    }
  },
  methods: {
    currentPageChange (val) {
      this.page = val
      //this.queryScore()
    },
    queryScore() {
      let requestBody = {
        major: this.major,
        grade: this.grade,
        term : this.term,
        page: this.page,
        pageSize: this.pageSize
      }
      request({
        url: apiUrl.adminGetScores,
        method: 'get',
        requestBody
      }).then(res => {
        const result = res.data
        if (result.code === globalRespCode.SUCCESS) {
          this.scores = result.data.scores
          this.total = result.data.total
        } else {
          this.$message({type: 'error', message: '获取成绩列表失败！'})
        }
      }).catch(e => {
        this.$message({type: 'error', message: '网路错误！'})
      })
    },

    getDetails (row) {
      this.getDetailsVisible = true
      let requestBody = {
        id: row.id,
        term: this.term,
      }
      request({
        url: apiUrl.adminGetScoreDetails,
        method: 'get',
        requestBody
      }).then(res => {
        const result = res.data
        if(result.code === globalRespCode.SUCCESS) {
          this.scoreDetails = result.scoreDetails
        } else {
          this.$message({type: 'error', message: '获取成绩单失败！'})
        }
      }).catch(error => {
        this.$message({type: 'error', message: '网络错误'})
      })
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange() {
      console.log("hh")
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // rankFormatter (row, column) {
    //   return
    // }
  },
  computed: {
    ...mapGetters(['majors'])
  }
}
</script>

